<template>
	<view class="container">
		<image class="logo"
		       src="/static/img/login.png"></image>
		<view class="title">{{title}}</view>
		<view class="login-box">
			<view class="cu-form-group">
				<text class="cuIcon-my input-icon"></text>
				<input placeholder-style="color:rgba(255, 255,255, 0.7);" placeholder="请输入账号" name="input" :value="user.username"  @input="(e)=>{user.username=e.detail.value}"></input>
			</view>
			<view class="cu-form-group" >
				<text class="cuIcon-lock input-icon"></text>
				<input type="password" placeholder-style="color:rgba(255, 255,255, 0.7);" placeholder="请输入密码" name="input" :value="user.password" @input="(e)=>{user.password=e.detail.value}"></input>
			</view>
			<view class="flex flex-direction" style="margin-top:100rpx">
				<button class="cu-btn lg" @click="login">登录</button>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		userAPI
	} from "api/index.js"
	export default {
		data() {
			return {
				title: process.uniEnv['APP_NAME'],
				user: {
					username: '',
					password: ''
				}
			}
		},
		onLoad() {
		},
		methods: {
			login() {
				userAPI.login({
					username: this.user.username,
					password: this.user.password,
				}).then(res => {
					if (res.code == 200) {
						uni.showToast({
							icon:'none',
							title:'登录成功',
							success:()=>{
								uni.setStorageSync('token', res.data);
								uni.setStorageSync('username', this.user.username);
								uni.setStorageSync('roleId', res.roleId)
								uni.setStorageSync('deptId', res.deptInfo.deptId);
								uni.setStorageSync('deptName', res.deptInfo.deptName)
								uni.redirectTo({
									url: '../index/index'
								})
							}
						})
					} 
				})
	
			}
		}
	}
</script>

<style scoped lang="scss">
	.container {
		height: 100vh;
		background: linear-gradient(180deg, #265AD2, #4695F9);
		text-align: center;
	}
	.cu-form-group+.cu-form-group{
		border-top:none;
	}
	.cu-form-group{
		background: transparent;
		border-bottom: 1px solid rgba(255, 255, 255, 0.7);
		input{
			color:#fff;
			text-align: left;
			margin-left: 10rpx;
		}
		.input-icon{
			color:rgba(255, 255, 255, 0.7);
		}
	}

	image {
		width: 194rpx;
		height: 198rpx;
		margin: 70rpx auto 0;
	}

	.title {
		font-size: 28rpx;
		font-family: SimSun;
		font-weight: 400;
		color: rgba(255, 255, 255, 0.7);
		margin-top: 10rpx;
	}

	.login-box {
		margin: 96rpx;
	}

	/deep/.van-cell {
		background-color: transparent;
	}

	/deep/ .van-icon:before {
		color: #fff !important;
	}

	/deep/.van-field__control {
		color: #fff;
	}

	/deep/.van-button--default {
		font-size: 32rpx;
		font-family: Source Han Sans CN;
		font-weight: 400;
		color: #3576E5;
	}
</style>
